import React from 'react';

import galoa from '../assets/galoa.svg';
import { Text, Span } from '../styles/global.styles'

import * as S from './Footer.styles'

const Footer = () => {
  return (
    <S.FooterContainer>
      <S.LeftContainer>
        <img
          src={galoa}
          alt="Logo Galo anais e proceedings"
          with="130px"
          height="60px"
        />
        <S.ButtonSaibamais>Saiba mais</S.ButtonSaibamais>
      </S.LeftContainer>
        <div>
          <Span fBold>Preservar a memria do evento e ampliar o acesso ao conhecimento cientfico </Span>
          <Span>gerado em eventos  a razo de ser da plataforma Galo Proceedings.</Span>
          <Text>
            Os trabalhos publicados aqui tm maior alcance e ficam disponveis para toda a comunidade cientfica,
            mantendo acesso o debate cientfico fomentado pelos encontros e aumentando o impacto do evento.
          </Text>
        </div>
    </S.FooterContainer>
  );
}

export default Footer;
