import React, { useState } from 'react';

import { textViewMore } from '../../../../constants/constants'

import {
    TextTitle,
    TitletWrapper,
    Text,
    WrapperContent,
    Span
} from '../../../../styles/global.styles'

import * as S from './Resume.styles';

const Resume = () => {
    const [isViewMore, setIsViewMore] = useState(false)

    return (
        <S.ResumeContainer>
            <TitletWrapper>
                <TextTitle>Resumo</TextTitle>
            </TitletWrapper>
            <WrapperContent>
                <Text fSizeMobile="10px">
                    A sequncia de edies do Simpsio Latino Americano de Cincia de Alimentos, desde 1995,
                    nos d o privilgio, a ns pesquisadores, de contar com um observatrio
                    de desenvolvimento da rea de Alimentos, com impacto na indstria de Alimentos,
                    com a importncia social e econmica da rea de Alimentos, com abrangncia nacional e internacional
                    {!isViewMore && (
                        <>
                        <Span>... </Span>
                        <S.ViewMoreButton onClick={() => setIsViewMore(!isViewMore)}>
                            ver mais
                        </S.ViewMoreButton>
                        </>
                    )}
                    {isViewMore && (
                        <>
                            <Span fSizeMobile="10px">{textViewMore}</Span>
                            <S.ViewMoreButton onClick={() => setIsViewMore(!isViewMore)}>
                                ver menos
                            </S.ViewMoreButton>
                        </>
                    )}
                </Text>
            </WrapperContent>
        </S.ResumeContainer>
    );
}

export default Resume;
